h1{
    text-align: center;
    color: #222222EE;
}
hr{
    border: none;
    border-bottom: 1px solid #ffffffee;
}
ul{
    padding: 0;
    width: 100%;
}
li{
    padding: 5px 10px;
    margin-bottom: 2.5px;
    border-radius: 3px;
    background: #22222222;
    word-wrap: break-word;
    width: 100%;
}
button{
    float: right;
    background: rgba(255, 18, 18, 0.637);
    border: none;
    color: rgba(246, 246, 246, 0.753);
    font-size: 1.1em;
    box-shadow: 2px 3px 5px 2px #22222255;
}
button:hover{
    background: rgb(255, 18, 18);
    color: #fff;
}
input{
    border-radius: 2px;
    outline: none;
    border: none;
    padding: 10px;
    background: #22222255;
    color: #FFF;
    width: 100%;
}
input::placeholder{
    color: #FFF;
    opacity: .55;
}
form{
    width: 100%;
}
div{
    width: 80vw;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}